@import "fonts";
@import "vars";
@import "mixins";


*, *:before, *:after {
	box-sizing: border-box;
}

ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

a {
	text-decoration: none;
}

html {
	width: 100%;
	height: 100%;
	font-family: $main-ff;
	font-size: 10px;
}

body {
	width: 100%;
	height: 100%;
	font-size: 1.8rem;
	line-height: 160%;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	
	&.body-fixed {
		overflow: hidden;
	}
}

.wrapper {
	position: relative;
	overflow-x: hidden;
}

.container {
	max-width: 1170px;
	width: 100%;
	height: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.section {
	margin-left: -15px;
	margin-right: -15px;
	padding-left: 15px;
	padding-right: 15px;
}

.title {
	margin: 0;
	font-family: $second-ff;
	font-weight: 700;
	font-size: 4rem;
	color: $darkblue;
	line-height: 110%;
}

// Imported Buttons
@import "buttons";

// // Imported Blocks
@import "blocks/header";
@import "blocks/intro";
@import "blocks/about";
@import "blocks/features";
@import "blocks/testimonials";
@import "blocks/footer";
