.intro {
    min-height: 100vh;
    padding-top: 20rem;
    padding-bottom: 30rem;
    background: linear-gradient(180deg, #f6fafd 0%, $white 85%);

    @media screen and (max-width: 767px) {
        padding-top: 15rem;
        padding-bottom: 15rem;
    }
    @media screen and (max-width: 576px) {
        padding-top: 10rem;
        padding-bottom: 10rem;
    }
    
    // .intro__inner

    &__inner {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: stretch;
    }

    // .intro__content

    &__content {
        flex: 1 0 50%;
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        align-items: flex-start;

        @media screen and (max-width: 1170px) {
            flex-basis: 70%;
        }
        @media screen and (max-width: 992px) {
            flex-basis: 80%;
        }
        @media screen and (max-width: 767px) {
            flex-basis: 100%;
        }
    }

    // .intro__title

    &__title {
        font-size: 8rem;
        margin-bottom: 50px;
        text-align: left;

        @media screen and (max-width: 992px) {
            font-size: 7rem;
        }
        @media screen and (max-width: 576px) {
            font-size: 6rem;
        }
        @media screen and (max-width: 476px) {
            margin-bottom: 30px;
            font-size: 5.3rem;
        }
    }

    // .intro__text

    &__text {
        margin: 0 0 50px;
        padding-right: 200px;
        font-size: 1.8rem;
        color: $grey;
        text-align: left;

        @media screen and (max-width: 576px) {
            padding-right: 150px;
        }
        @media screen and (max-width: 476px) {
            padding-right: 0;
            margin-bottom: 30px;
        }
    }

    // .intro__image

    &__image {
        flex: 1 0 50%;
        position: relative;

        @media screen and (max-width: 576px) {
            display: none;
        }
    }

    // .intro__bg

    &__bg {
        position: absolute;
        bottom: 0;
        left: 0;
        transform: translate(-200px, 150px);
    }
}
