.footer {
    position: relative;
    padding-top: 100px;
    padding-bottom: 50px;
    border-top: 2px solid rgba($purple, 0.2);
    overflow-y: hidden;

    @media screen and (max-width: 992px) {
        padding-top: 60px;
    }

    // .footer__inner

    &__inner {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: flex-start;
    }

    // .footer__tagline

    &__tagline {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 150px;

        @media screen and (max-width: 992px) {
            margin-bottom: 80px;
        }
        @media screen and (max-width: 767px) {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 50px;
        }
        
    }

    // .footer__main

    &__main {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        margin-bottom: 90px;

        @media screen and (max-width: 767px) {
            flex-wrap: wrap;
            margin-bottom: 40px;
        }
    }

    // .footer__info

    &__info {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @media screen and (max-width: 767px) {
            flex-grow: 1;
        }
    }

    // .footer__logo

    &__logo {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 65px;

        @media screen and (max-width: 767px) {
            flex-basis: 50%;
        }
        @media screen and (max-width: 576px) {
            margin-bottom: 50px;
        }

        &-img {
            margin-right: 20px;
        }
    }

    // .footer__about

    &__about {
        @media screen and (max-width: 576px) {
            
        }
    }


    // .footer__help

    &__help {
    }

    // .footer__social

    &__social {
        padding-right: 20px;
    }

    // .footer__copy

    &__copy {
    }
}
.tagline {
    // .tagline__text

    &__text {
        font-weight: 500;
        font-size: 1.8rem;
        color: $grey;

        @media screen and (max-width: 767px) {
            margin-bottom: 25px;
        }
    }

    // .tagline__title

    &__title {
        font-weight: 700;
        font-size: 4rem;
        margin-bottom: 20px;
    }

    // .tagline__buttons

    &__buttons {
        display: flex;
        align-items: center;

        @media screen and (max-width: 992px) {
            // flex-direction: column;
            // align-items: flex-start;
        }
    }
}
.logo {
    // .logo__img

    &__img {
    }

    // .logo__descr

    &__descr {
        font-weight: 900;
        font-size: 2rem;
        color: $darkblue;
    }
}
.contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @media screen and (max-width: 767px) {
        flex-grow: 1;
    }

    // .contacts__address

    &__address {
        margin-bottom: 15px;
        font-size: 1.6rem;
        font-weight: 500;
        color: $darkblue;
    }
}
.mail {
    font-weight: 300;
    font-size: 1.6rem;
    color: $darkblue;
}
.phone {
    font-weight: 300;
    font-size: 1.6rem;
    color: $darkblue;
}
.footer-about {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 100px;

    @media screen and (max-width: 992px) {
        padding-left: 20px;
    }
    @media screen and (max-width: 767px) {
        flex-grow: 1;
        padding-left: 0;
    }
    @media screen and (max-width: 576px) {
        flex-basis: 100%;
        margin-top: 40px;
    }
    // .footer-about__header

    &__header {
        margin-top: 5px;
        margin-bottom: 35px;
        font-size: 1.6rem;
        font-weight: 900;
        color: $darkblue;
    }

    // .footer-about__list

    &__list {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
    }

    // .footer-about__item

    &__item {
        display: inline-block;
        margin-bottom: 10px;
        border-bottom: 1px solid transparent;
        transition: border-bottom $link-transition linear;
        &:last-child {
            margin-bottom: 0;
        }
        &:hover {
            border-bottom: 1px solid #000;
        }
    }

    // .footer-about__link

    &__link {
        font-weight: 400;
        font-size: 1.6rem;
        color: $darkblue;
    }
}
.footer-help {

    @media screen and (max-width: 767px) {
        display: flex;
        flex-direction: column;
    }
    @media screen and (max-width: 576px) {
        flex-basis: 100%;
        margin-top: 40px;
    }

    // .footer-help__header

    &__header {
        margin-top: 5px;
        margin-bottom: 35px;
        font-size: 1.6rem;
        font-weight: 900;
        color: $darkblue;

        @media screen and (max-width: 576px) {
            margin-bottom: 20px;
        }
    }

    // .footer-help__list

    &__list {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
    }

    // .footer-help__item

    &__item {
        display: inline-block;
        margin-bottom: 10px;
        border-bottom: 1px solid transparent;
        transition: border-bottom $link-transition linear;
        &:last-child {
            margin-bottom: 0;
        }
        &:hover {
            border-bottom: 1px solid #000;
        }
    }

    // .footer-help__link

    &__link {
        font-weight: 400;
        font-size: 1.6rem;
        color: $darkblue;
    }
}
.footer-social {
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 767px) {
        justify-content: flex-start;
        flex-basis: 100%;
        margin-top: 40px;
    }
    @media screen and (max-width: 576px) {
        flex-basis: auto;
    }

    // .footer-social__header

    &__header {
        margin-top: 5px;
        margin-bottom: 35px;
        font-size: 1.6rem;
        font-weight: 900;
        color: $darkblue;

        @media screen and (max-width: 767px) {
            margin-bottom: 20px;
        }
    }

    // .footer-social__list

    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @media screen and (max-width: 767px) {
            justify-content: flex-start;
        }
    }

    // .footer-social__link

    &__link {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 15px;
        border-radius: 50%;

        &:last-child {
            margin-right: 0;
        }

        &:hover .social-icon {
            circle {
                fill: rgba($purple, 1);
            }
        }
    }
}
.social-icon {
    circle {
        transition: fill 0.25s linear;
        fill: rgba($darkblue, 0.15);
    }
    path {
        fill: $white;
    }
}
.copyright {
    display: flex;
    justify-content: space-between;
    align-items: center;
    // .copyright__text

    &__text {
        font-weight: 300;
        font-size: 1.2rem;
        line-height: 180%;
        color: $darkblue;
    }
}
.footer-message-btn {
    & svg {
        transition: transform 0.25s linear;
        &:hover {
            transform: scale(1.1);
        }
    }
}
