.header {
    $parent: &;

    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    z-index: 99;

    @media screen and (max-width: 767px) {
        position: fixed;
        top: 0;
        background-color: darken($white, 0.6%);
        box-shadow: 0 2px 8px darken($white, 10%);
    }

    &.scroll {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        background-color: darken($white, 0.6%);
        box-shadow: 0 2px 8px darken($white, 10%);
    }

    // .header__inner

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;

        #{$parent}.scroll & {
            padding: 15px 0;
        }

        @media screen and (max-width: 767px) {
            padding: 15px 0;
        }
    }
}
.logo {
    display: block;
    // .logo__img
    &__img {
        display: block;
    }
}
.nav {
    flex: 1 0 auto;
    padding-left: 11%;

    @media screen and (max-width: 767px) {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        height: calc(100vh - 65px);
        padding: 50px 15px 15px;
        background-color: darken($white, 0.6%);
        transform: translateX(-100%);
        transition: transform 0.4s ease-in;
        overflow: auto;
    }

    &.header__nav.show {
        transform: translateX(0);
    }

    // .nav__list

    &__list {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        @media screen and (max-width: 767px) {
            flex-direction: column;
        }
    }

    // .nav__item

    &__item {
        margin-right: 40px;

        &:last-child {
            margin-right: 0;
        }

        @media screen and (max-width: 767px) {
            margin: 0 0 30px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    // .nav__link

    &__link {
        display: inline-block;
        position: relative;
        font-size: 1.6rem;
        font-weight: 900;
        color: rgba($grey, 0.7);
        transition: $link-transition linear;
        &:hover {
            color: $darkblue;
            transform: translateY(-2px);

            @media screen and (max-width: 767px) {
                color: $purple;
            }
        }

        @media screen and (max-width: 767px) {
            padding: 10px;
            font-size: 2rem;
        }

        // .nav__link--current

        &--current {
            color: $darkblue;
            transform: translateY(-2px);

            @media screen and (max-width: 767px) {
                color: $purple;
            }
        }
    }
}
