.btn {
    display: inline-block;
    font-size: 1.8rem;
    font-weight: 900;
    padding: 1.6rem 3.7rem;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    border: none;
    &:focus {
        outline: none;
    }
}

.request-btn {
    padding-right: 7rem;
    padding-left: 2.9rem;
    font-family: $main-ff;
    font-size: 1.6rem;
    color: $darkblue;
    background-color: $white;
    box-shadow: 0px 5px 5px rgba(75, 93, 104, 0.1);

    &:active {
        transform: translateY(1px);
        box-shadow: 0px 4px 4px rgba(75, 93, 104, 0.1);
    }
}

.header__request-btn {
    @media screen and (max-width: 767px) {
        display: none;
    }
}

.btn--arrow {
    position: relative;
    padding-right: 7rem;
    &:after {
        content: "";
        position: absolute;
        top: 50%;
        right: 1.5rem;
        background: url("../images/icons/arrow-r.svg") no-repeat center /
            contain;
        width: 50px;
        height: 15px;
        transform: translateY(-50%);
    }
    &:hover:after {
        animation: 1.5s btn_arrow_animation infinite;
    }
}

@keyframes btn_arrow_animation {
    50% {
        transform: translate(6px, -50%);
    }
}

.btn--round {
    border-radius: 50px;
}

// .intro__button
.intro__button {
    font-weight: 900;
    color: $white;
    background-color: $purple;
    transition: background-color .3s ease;
	&:hover {
		background-color: darken($purple, 11%);
	}
	&:active {
		transform: translateY(1px);
	}
}

// .tagline__btn

.tagline__btn {
	margin-right: 30px;
	background-color: #F063B8;
	color: $white;
	transition: background-color .3s linear;
	&:hover {
		background-color: darken(#F063B8, 12%);
	}
	&:active {
		transform: translateY(1px);
    }
    @media screen and (max-width: 992px) {
        display: none;
    }
}

.menu-btn {
    position: relative;
    right: 5px;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
	width: 25px;
	height: 22px;
	padding: 0;
	border: none;
	background-color: transparent;
	cursor: pointer;
	&:focus {
        outline: none;
    }
	span {
		width: 25px;
		height: 4px;
		border-radius: 8px;
		background-color: $purple;
        transition: width .2s linear, transform .2s linear;
    }
    &.active {
        justify-content: center;
        height: 25px;
    }
	&.active span {
        position: absolute;
        width: 26px;
    }
	&.active span:nth-child(2) {
        display: none;
    }
	&.active span:first-child {
        transform: rotate(45deg);
    }
	&.active span:last-child {
        transform: rotate(-45deg);
    }
	@media screen and (max-width: 767px) {
        display: flex;
    }
}