.about {
    border-radius: 50px;
    background: rgba(240, 99, 184, 0.15);

    // .about__inner

    &__inner {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: stretch;
    }

    // .about__image

    &__image {
        flex: 0 1 50%;
        padding-right: 30px;

        @media screen and (max-width: 992px) {
            flex-basis: 40%;
            padding: 70px 20px 70px 0;
		}
		@media screen and (max-width: 767px) {
            padding: 0 20px 70px;
		}
		@media screen and (max-width: 576px) {
            display: none;
        }
    }

    // .about__bg

    &__bg {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    // .about__content

    &__content {
        flex: 0 0 50%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 125px 90px 125px 0;

        @media screen and (max-width: 1170px) {
            padding: 90px 50px 90px 0;
        }

        @media screen and (max-width: 992px) {
            flex-basis: 60%;
            padding: 60px 20px 60px 0;
		}
		
		@media screen and (max-width: 767px) {
            padding: 50px 0px 50px;
		}
		@media screen and (max-width: 576px) {
            flex-basis: 100%;
        }
    }

    // .about__title

    &__title {
		margin-bottom: 4rem;
		
		@media screen and (max-width: 576px) {
            font-size: 3.5rem;
		}
		@media screen and (max-width: 476px) {
            font-size: 3.2rem;
        }
    }

    // .about__text

    &__text {
        flex-grow: 1;
        margin: 0;
        font-weight: 500;
        font-size: 1.8rem;
        color: $grey;

        @media screen and (max-width: 992px) {
            //padding-right: 15px;
        }
    }
}
