.testimonials {
    margin-bottom: 150px;
    padding: 110px 90px 60px;
    border-radius: 50px;
    background: $purple;

    @media screen and (max-width: 992px) {
        margin-bottom: 90px;
        padding: 70px 70px 40px;
    }

    @media screen and (max-width: 767px) {
        margin-bottom: 60px;
        padding: 50px 40px 40px;
    }
    @media screen and (max-width: 576px) {
        margin-bottom: 50px;
        padding: 40px 5px 30px;
    }

    // .testimonials__inner

    &__inner {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @media screen and (max-width: 767px) {
            align-items: center;
        }
    }

    // .testimonials__title

    &__title {
        margin-bottom: 85px;
        text-align: left;
        color: $white;

        @media screen and (max-width: 767px) {
            margin-bottom: 60px;
        }
    }

    // .testimonials__slider

    &__slider {
        width: 100%;
        height: 100%;
        padding-bottom: 50px;

        & .slick-list {
            border-radius: 20px;
            padding-bottom: 20px;
        }
        & .slick-arrow {
            position: absolute;
            right: 0;
            bottom: 0;
            width: 40px;
            height: 14px;
            font-size: 0;
            border: none;
            outline: none;
            cursor: pointer;
            background: transparent;
            transition: all 0.1s linear;
            &:hover {
                transform: scale(1.1);
            }

            @media screen and (max-width: 767px) {
                right: 10px;
            }

            &.slick-prev {
                right: 55px;
                background: url(../images/icons/slider-arrow-left.svg) no-repeat
                            100% / contain;
            }
            &.slick-next {
                background: url(../images/icons/slider-arrow-right.svg)
                            no-repeat 100% / contain;
                &.slick-disabled {
                    right: 10px;
                }
            }
            &.slick-disabled {
                width: 30px;
                opacity: 0.7;
            }
        }

        & .slick-dots {
            position: absolute;
            left: 0;
            bottom: 0;
            display: flex;
            align-items: center;

            @media screen and (max-width: 767px) {
                left: 10px;
            }
            @media screen and (max-width: 576px) {
                left: 50%;
                bottom: 5px;
                transform: translateX(-50%);
            }

            li {
                margin-right: 10px;
                font-size: 0;

                @media screen and (max-width: 576px) {
                    margin-right: 20px;
                }

                &:last-child {
                    margin-right: 0;
                }

                &.slick-active button {
                    width: 12px;
                    height: 12px;
                    background-color: #f063b8;

                    @media screen and (max-width: 767px) {
                        width: 18px;
                        height: 18px;
                    }
                }

                & button {
                    display: block;
                    width: 7px;
                    height: 7px;
                    padding: 0;
                    border: none;
                    border-radius: 50%;
                    font-size: 0;
                    outline: none;
                    transition: transform 0.1s linear;
                    &:hover {
                        cursor: pointer;
                        transform: scale(1.15);
                    }

                    @media screen and (max-width: 767px) {
                        width: 14px;
                        height: 14px;
                    }
                }
            }
        }
    }

    // .testimonials__item

    &__item {
    }

    // .testimonials__icon

    &__icon {
    }

    // .testimonials__content

    &__content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-left: 30px;

        @media screen and (max-width: 576px) {
            padding-left: 0;
            align-items: center;
        }
    }
}
.slick-dotted.slick-slider {
    margin-bottom: 0;
}
.testimonials__slider .person {
    display: flex;
}
.person {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    max-width: 645px;
    margin-right: 20px;
    padding: 60px 70px 50px;
    border-radius: 20px;
    background: $white;
    outline: none;
    opacity: 0.85;
    box-shadow: 0px 20px 0 rgba(0, 0, 0, 0.08);

    &:last-child {
        margin-right: 0;

        @media screen and (max-width: 992px) {
            margin-right: 0;
        }
    }

    @media screen and (max-width: 992px) {
        max-width: none;
        margin-right: 10px;
        margin-left: 10px;
    }
    @media screen and (max-width: 767px) {
        padding: 35px;
    }
    @media screen and (max-width: 576px) {
        flex-direction: column;
        flex-wrap: nowrap;
        align-items: center;
    }
    @media screen and (max-width: 476px) {
        margin-right: 2px;
        margin-left: 2px;
        padding: 20px 20px 30px;
    }

    &.slick-active {
        opacity: 1;
        box-shadow: 0px 20px 0 rgba(0, 0, 0, 0.15);
    }
    // .person__icon

    &__icon {
        flex-shrink: 0;
        width: 90px;
        height: 90px;
        border-radius: 50%;
        overflow: hidden;
    }

    // .person__photo

    &__photo {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    // .person__name

    &__name {
        margin: 0;
        padding-top: 20px;
        font-weight: 900;
        font-size: 1.8rem;
    }

    // .person__position

    &__position {
        font-weight: 500;
        font-size: 1.4rem;
        color: $purple;
        margin-bottom: 20px;
    }

    // .person__text

    &__text {
        margin-top: 0;
        margin-bottom: 0;
        font-size: 1.8rem;
        font-weight: 400;
        line-height: 180%;
        color: $grey;
    }
}
