.features {
    padding-top: 15rem;
    padding-bottom: 15rem;

    @media screen and (max-width: 992px) {
        padding-top: 10rem;
        padding-bottom: 10rem;
    }

    @media screen and (max-width: 767px) {
        padding-top: 7rem;
        padding-bottom: 7rem;
    }
    @media screen and (max-width: 576px) {
        // padding-top: 10rem;
        // padding-bottom: 10rem;
    }

    // .features__inner

    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    // .features__title

    &__title {
        margin-bottom: 5rem;
        text-align: center;

        @media screen and (max-width: 992px) {
            margin-bottom: 3rem;
        }
    }

    // .features__subtitle

    &__subtitle {
        max-width: 620px;
        margin-bottom: 10rem;
        font-weight: 500;
        color: $grey;
        text-align: center;

        @media screen and (max-width: 992px) {
            margin-bottom: 6rem;
        }
    }
}
.advantages {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: stretch;
    margin-bottom: -5rem;

    // .advantages__item

    &__item {
        flex: 0 0 50%;
        display: flex;
        align-items: stretch;
        margin-bottom: 5rem;
        padding-right: 25px;

        &:nth-child(2n) {
            padding-left: 25px;
            padding-right: 0;
            
            @media screen and (max-width: 1170px) {
                  padding-left: 5px;
            }
        }

        @media screen and (max-width: 1170px) {
            padding-right: 5px;
        }

        @media screen and (max-width: 992px) {
            flex-basis: 100%;
            padding-right: 0;
        }
    }

    // .advantages__image

    &__image {
        position: relative;
        flex: 0 0 90px;
        height: 100%;

        @media screen and (max-width: 476px) {
            display: none;
        }
    }

    // .advantages__photo

    &__photo {
        position: absolute;
        top: 60px;

        @media screen and (max-width: 576px) {
            width: 200px;
        }
    }

    // .advantages__content

    &__content {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 5rem 5rem 3rem 17rem;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100% 100%;

        @media screen and (max-width: 992px) {
            padding-right: 8rem;
            padding-left: 20rem;
        }
        @media screen and (max-width: 576px) {
            padding-right: 2.5rem;
            padding-left: 13rem;
        }
        @media screen and (max-width: 476px) {
            padding-right: 2rem;
            padding-left: 7rem;
        }
    }

    // .advantages__title

    &__title {
        margin-bottom: 2rem;
        font-size: 2.4rem;
        font-weight: 400;
        color: $darkblue;
    }

    // .advantages__text

    &__text {
        flex: 1 0 auto;
        margin-bottom: 2rem;
        font-size: 1.6rem;
        font-weight: 300;
        color: $grey;
    }

    // .advantages__btn

    &__btn {
        font-size: 1.6rem;
        padding-left: 0;
        color: $darkblue;
        white-space: nowrap;
        background-color: transparent;
    }
}
